import { useState, useEffect, useRef } from "react";
import { useLiveChat, useToken } from "../store/chat.state";
import { fetchLiveConversationHistory, fetchLiveConversationsByBotId } from "./live.service";

function useLiveChatHook() {
  //const tokenStore = useToken();
  const token = useToken((state) => state.token);
  const conversationId = useToken((state) => state.conversationId);
  const sessionId = useToken((state) => state.sessionId);
  const activeLiveChatConversationMap = useLiveChat(
    (state) => state.activeLiveChatConversationMap
  );
  const liveChatMessages = useLiveChat((state) => state.liveChatMessages);
  const addHistoryMessagesToState = useLiveChat(
    (state) => state.addHistoryMessagesToState
  );
  const clearLiveChatMessages = useLiveChat(
    (state) => state.clearLiveChatMessages
  );
  const storeLiveConversationIdsBySessionId = useLiveChat(
    (state) => state.storeLiveConversationIdsBySessionId
  );

  const [isLiveActive, setIsLiveActive] = useState(false);
  const [shouldFetchMessages, setShouldFetchMessages] = useState(false);
  const [isLiveChatLoading, setIsLiveChatLoading] = useState(false);
  const intervalRef = useRef(null); // Ref to store interval ID

  useEffect(() => {
    const fetchLiveConversationsForSession = async () => {
      if (!token) return;

      try {
        console.log(
          `[useLiveChatHook] Fetching live conversations for sessionId: ${sessionId}`
        );
        const conversations = await fetchLiveConversationsByBotId(token);

        // Assume conversations is an array of { conversationId: string }
        console.log(
          "[useLiveChatHook] Fetched live conversations:",
          conversations
        );

        storeLiveConversationIdsBySessionId(conversations.liveConversations); // Store in Zustand
      } catch (error) {
        console.error(
          "[useLiveChatHook] Error fetching live conversations:",
          error
        );
      }
    };

    fetchLiveConversationsForSession();
  }, [token, storeLiveConversationIdsBySessionId]);

  useEffect(() => {
    const loadInitialHistory = async () => {
      setIsLiveChatLoading(true); // Set loading to true before starting the fetch
      try {
        console.log(
          `[useLiveChatHook] Loading initial history for conversationId: ${conversationId}`
        );
        clearLiveChatMessages();
        const result = await fetchLiveConversationHistory(
          token,
          conversationId
        );
        addHistoryMessagesToState(result?.messages);
        console.log("[useLiveChatHook] Initial history loaded:", result);
      } catch (error) {
        console.error(
          "[useLiveChatHook] Error loading initial history:",
          error
        );
      } finally {
        setIsLiveChatLoading(false); // Set loading to false after fetching completes
      }
    };

    if (conversationId && isLiveActive) {
      loadInitialHistory();
    }
  }, [addHistoryMessagesToState, clearLiveChatMessages, conversationId, isLiveActive]);

  useEffect(() => {
    const checkLiveActive = conversationId in activeLiveChatConversationMap;
    setIsLiveActive(checkLiveActive);

    console.log("[useLiveChatHook] isLiveActive updated:", checkLiveActive);
  }, [activeLiveChatConversationMap, conversationId]);

  useEffect(() => {
    if (isLiveActive 
      //&& liveChatMessages.length !== 0
    ) {
      setShouldFetchMessages(true);
      console.log(
        "[useLiveChatHook] shouldFetchMessages set to true: Live chat is active and there are messages"
      );
    } else {
      setShouldFetchMessages(false);
      console.log(
        "[useLiveChatHook] shouldFetchMessages set to false: Live chat is inactive or there are no messages"
      );
    }
  }, [isLiveActive, liveChatMessages]);

  //Interval for fetching new live message 
  useEffect(() => {
    if (shouldFetchMessages && !intervalRef.current) {
      // console.log(
      //   "[useLiveChatHook] Starting to fetch messages every 5 seconds..."
      // );

      intervalRef.current = setInterval(async () => {
        // const { token, conversationId } = tokenStore;

        try {
          // console.log(
          //   `[useLiveChatHook] Fetching messages for conversationId: ${conversationId}`
          // );
          const result = await fetchLiveConversationHistory(
            token,
            conversationId
          );
          addHistoryMessagesToState(result?.messages);
          // console.log("[useLiveChatHook] Fetched messages:", result);
        } catch (error) {
          console.error("[useLiveChatHook] Error fetching messages:", error);
        }
      }, 3000); // 5-second interval
    }

    // Cleanup function for the interval
    return () => {
      if (intervalRef.current) {
        console.log("[useLiveChatHook] Stopping message fetch interval...");
        clearInterval(intervalRef.current);
        intervalRef.current = null; // Clear the reference
      }
    };
  }, [shouldFetchMessages]);
  
  return{
    isLiveActive,
    isLiveChatLoading
  }
}

export default useLiveChatHook;
