import { useEffect, useState } from 'react'
import { useChat, useToken, useWidgetCustomization } from '../store/chat.state'

// Custom hook for counter functionality
export default function useInitChat() {
  
  const [isTimerActive , setIsTimerActive] = useState(false)

  const chat = useChat();
  const tokenStore = useToken();
  const widgetCustomization = useWidgetCustomization()

  useEffect(()=>{
    if (!isTimerActive || chat.messages.length > 1) return

    let greetMessages = widgetCustomization?.uiConfigs?.greetMsgs
    let initCounter = 0;
    let messageLength = greetMessages?.length 
    let timer = setInterval(()=>{
      if (initCounter === messageLength) {
        clearInterval(timer); 
        setIsTimerActive(false);  
        return;
      }
        chat.appendMessage({
          id: initCounter,
          body: greetMessages[initCounter] || "Hello",
          sender: "bot",
          hasFeedback: false,
          time: Date.now(),
        });
        initCounter ++
    },2000 / messageLength)
  },[widgetCustomization?.uiConfigs?.greetMsgs , isTimerActive]);


 const restartInitChat=()=>{
   console.log("hereee")
    chat.restartState();
    tokenStore.setConversationId(null);
    let greetMessages = widgetCustomization?.uiConfigs?.greetMsgs
    let initCounter = 0;
    let messageLength = greetMessages?.length 
    let timer = setInterval(()=>{
      if (initCounter === messageLength) {
        clearInterval(timer); 
        setIsTimerActive(false);  
        return;
      }
        chat.appendMessage({
          id: initCounter,
          body: greetMessages[initCounter] || "Hello",
          sender: "bot",
          hasFeedback: false,
          time: Date.now(),
        });
        initCounter ++
    },2000 / messageLength)
  }

  return[setIsTimerActive,restartInitChat,isTimerActive]
}

