import { create } from "zustand";
import { v4 as uuidv4 } from "uuid";

const initialMessage = {
  id: uuidv4(),
  body: null,
  sender: "bot",
  hasFeedback: false,
  time: Date.now(),
};

export const useChat = create((set, get) => ({
  messages: [initialMessage],
  restartState: () => {
    set((state) => ({
      messages: [initialMessage],
    }));
  },

  appendMessage: (newMessage) =>
    set((state) => {
      let lastMessageIndex = state.messages.length - 1;

      return state.messages[lastMessageIndex].body
        ? { messages: [...state.messages, newMessage] }
        : { messages: [...state.messages.slice(0, -1), newMessage] };
    }),
  streamChunkToLastMessage: (chunk) =>
    set((state) => {
      const lastItemIndex = state.messages.length - 1;
      const lastItemValue = state.messages[lastItemIndex];
      if (lastItemIndex < 0) {
        // If the array is empty, do nothing
        return state;
      }

      const newMessages = [...state.messages];
      newMessages[lastItemIndex] = {
        ...newMessages[lastItemIndex],
        body: lastItemValue.body + chunk,
      };

      return { messages: newMessages };
    }),
  handleLastMassageFailure: () =>
    set((state) => {
      const lastItemIndex = state.messages.length - 1;
      const lastItemValue = state.messages[lastItemIndex];

      const regex = /\bsorry\b/gi;

      if (lastItemValue.body.match(regex)) {
        console.log("MATCHED");
        const newMessages = [...state.messages];
        newMessages[lastItemIndex] = {
          ...newMessages[lastItemIndex],
          chatButtons: ["🙎‍♂️ Start LiveChat"],
        };
        return { messages: newMessages };
      } else {
        return { state };
      }
    }),
}));

export const useWidget = create((set) => ({
  isActive: false,
  //this state is for handling overall widget load state
  isFullyActive: false,
  toggle: () => set((state) => ({ isActive: state.isActive ? false : true })),
  close: () => set((state) => ({ isActive: false })),
  fullyDeactive: () => set((state) => ({ isFullyActive: false })),
  fullyActive: () => set((state) => ({ isFullyActive: true })),
}));

export const useController = create((set, get) => ({
  controller: new AbortController(),
  setController: (controller) => set({ controller }),
  abort: () => {
    const state = get();
    state.controller.abort();
    set({ controller: new AbortController() });
  },
  getSignal: () => {
    const state = get();
    return state.controller.signal;
  },
}));

export const useToken = create((set, get) => ({
  token: null,
  sessionId: null,
  conversationId: null,
  setToken: (newToken) => set((state) => ({ token: newToken })),
  setSessionId: (newSessionId) => set((state) => ({ sessionId: newSessionId })),
  setConversationId: (newConversationId) => {
    console.log("Setting conversationId:", newConversationId);
    set({ conversationId: newConversationId });
    return newConversationId; // return the new conversationId so you can use it immediately
  },

  setTokenAndSessionId: (newToken, newSessionId, newConversationId = null) =>
    set((state) => ({
      token: newToken,
      sessionId: newSessionId,
      ...(newConversationId !== null && { conversationId: newConversationId }), // Only set conversationId if provided
    })),
}));

