export const useLiveChat = create((set) => ({
  // Map to store the active state of live chat conversations
  activeLiveChatConversationMap: {},

  liveChatMessages: [],
  messageIdSet: new Set(),

  // Activate live chat for a conversation, setting the conversation as active
  activateLiveChatForConversation: (conversationId) =>
    set((state) => ({
      activeLiveChatConversationMap: {
        ...state.activeLiveChatConversationMap,
        [conversationId]: true, // Activate live chat for this conversation
      },
    })),

  // Append a new message to the live chat messages array
  appendMessageToLiveChat: (newMessage) =>
    set((state) => {
      // Create a new Set to avoid mutating the existing state directly
      const updatedMessageIdSet = new Set(state.messageIdSet);
      updatedMessageIdSet.add(newMessage.id); // Add the new message ID to the set

      return {
        liveChatMessages: [
          ...state.liveChatMessages,
          { ...newMessage }, // Append new message with conversationId
        ],
        messageIdSet: updatedMessageIdSet, // Update the messageIdSet
      };
    }),

  // Restart (clear all messages) for a specific live chat conversation
  restartLiveChatConversation: (conversationId) =>
    set((state) => {
      // Remove all messages for the specific conversationId
      const filteredMessages = state.liveChatMessages.filter(
        (message) => message.conversationId !== conversationId
      );
      return {
        liveChatMessages: filteredMessages,
      };
    }),
  addHistoryMessagesToState: (newMessages) => {
    set((state) => {
      // console.log("addHistoryMessagesToState called", { newMessages });

      if (newMessages?.length === 0) {
        console.log("No new messages provided. Exiting.");
        return state;
      }

      // Use the correct 'id' property from newMessages instead of 'messageId'
      const newMessageIds = new Set(newMessages.map((msg) => msg.id));
      // console.log("New message IDs:", [...newMessageIds]);

      if (
        newMessageIds.size === state.messageIdSet.size
        //NOTE: for more advanced message check logic
        // && [...newMessageIds].every(id => state.messageIdSet.has(id))
      ) {
        // console.log(
        //   "No new unique messages detected. State remains unchanged."
        // );
        // No new messages, no need to mutate state
        return state;
      }

      // Filter out the messages that are already present in the state
      const filteredMessages = newMessages.filter(
        (msg) => !state.messageIdSet.has(msg.id)
      );
      // console.log("Filtered messages (new to state):", filteredMessages);

      const updatedMessages = [...state.liveChatMessages, ...filteredMessages];
      // console.log("Updated liveChatMessages:", updatedMessages);

      const updatedMessageIdSet = new Set(state.messageIdSet);
      filteredMessages.forEach((msg) => updatedMessageIdSet.add(msg.id));
      // console.log("Updated messageIdSet:", [...updatedMessageIdSet]);

      return {
        liveChatMessages: updatedMessages,
        messageIdSet: updatedMessageIdSet,
      };
    });
  },

  // Check if live chat is active for a given conversation
  isLiveChatActive: (conversationId) => (state) =>
    conversationId && !!state?.activeLiveChatConversationMap?.[conversationId],

  // Fetch live chat conversation history and store it
  fetchLiveChatHistory: async (conversationId, botId, sessionId) => {
    try {
      const response = await fetch("/api/history", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          botId,
          sessionId,
        }),
      });

      if (!response.ok) {
        throw new Error("Failed to fetch live chat history");
      }

      const data = await response.json();

      // Check if data contains messages and update the state
      if (data && data.messages) {
        set((state) => {
          const existingMessages = state.liveChatMessages.filter(
            (message) => message.conversationId === conversationId
          );

          const updatedMessages = [
            ...existingMessages,
            ...data.messages.map((message) => ({
              ...message,
              conversationId,
            })),
          ];

          return {
            liveChatMessages: updatedMessages,
          };
        });
      }
    } catch (error) {
      console.error("Error fetching live chat history:", error);
    }
  },

  // Start an interval to fetch live chat messages for a specific conversation
  startFetchingLiveChatHistory: (conversationId, botId, sessionId) => {
    const intervalId = setInterval(() => {
      useLiveChat
        .getState()
        .fetchLiveChatHistory(conversationId, botId, sessionId);
    }, 5000); // Fetch every 5 seconds (can adjust as needed)

    return intervalId;
  },

  // Stop the interval (when needed)
  stopFetchingLiveChatHistory: (intervalId) => {
    clearInterval(intervalId);
  },
  storeLiveConversationIdsBySessionId: async (conversations) => {
    try {
      // Validate the input to ensure it's an array of { conversationId: string }
      if (
        !Array.isArray(conversations) ||
        conversations.some((c) => !c.conversationId)
      ) {
        throw new Error(
          "Invalid input: Expected an array of { conversationId: string }"
        );
      }

      set((state) => {
        // Update the state map directly to avoid creating unnecessary intermediate data structures
        const updatedMap = { ...state.activeLiveChatConversationMap };

        for (const { conversationId } of conversations) {
          updatedMap[conversationId] = true;
        }

        return { activeLiveChatConversationMap: updatedMap };
      });
    } catch (error) {
      console.error("Error updating live conversations in state:", error);
    }
  },

  clearLiveChatMessages: () =>
    set(() => ({
      liveChatMessages: [],
      messageIdSet: new Set(),
    })),
}));
