import axios from "axios";

export async function requestLiveConversation(conversationId, botId, sessionId) {
  try {
    const response = await axios.post(
      `${process.env.REACT_APP_NEST_BACK_URL}/live/request-chat`,
      {
        conversationId,
        botId,
        sessionId,
      },
      {
        withCredentials: true,
      }
    );

    const data = response.data;
    console.log({data});
    
    return data;
  } catch (error) {
    console.error("Error in requestLiveConversation:", error);
    throw new Error("Failed to request live conversation");
  }
}
export async function sendUserMessageLiveConversation(conversationId, message) {
  try {
    const response = await axios.post(
      `${process.env.REACT_APP_NEST_BACK_URL}/live/message/user`,
      {
        conversationId,
        message,
      },
      {
        withCredentials: true,
      }
    );

    const data = response.data;
    console.log({ data });

    return data;
  } catch (error) {
    console.error("Error in sendMessageToUser:", error);
    throw new Error("Failed to send user message");
  }
}