import axios from 'axios';

export async function fetchLiveConversationHistory(botId, conversationId) {
  try {
    const response = await axios.post(
      `${process.env.REACT_APP_NEST_BACK_URL}/live/history`,
      {
        botId,
        conversationId,
      },
      {
        withCredentials: true,
      }
    );

    const data = response.data;

    return data;
  } catch (error) {
    console.error("Error in fetchLiveConversationHistory:", error);
    throw new Error("Failed to fetch live conversation history");
  }
}

export async function fetchLiveConversationsByBotId(botId) {
  try {
    const response = await axios.post(
      `${process.env.REACT_APP_NEST_BACK_URL}/live/conversation-ids/widget`,
      {
        botId,
      },
      {
        withCredentials: true,
      }
    );

    const data = response.data;

    return data;
  } catch (error) {
    console.error("Error in fetchLiveConversations:", error);
    throw new Error("Failed to fetch live conversations");
  }
}
