import axios from "axios";

async function fetchBotConfigs(botId) {
  try {
    // Wrapping the axios call with retry logic
    const response = await retryFetch(
      () => axios.get(
      `${process.env.REACT_APP_NEST_BACK_URL}/widget/config/${botId}`,
      {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        }
      }
    ) , 3 , 500);

    const data = response.data;
    return data;
  } catch (error) {
    console.error("Error fetching bot configs:", error);
    throw error; // Propagate the error after all retries fail
  }
}

const retryFetch = async (fetchFunc, retries = 3, delay = 1000) => {
  let lastError;
  for (let i = 0; i < retries; i++) {
    try {
      const result = await fetchFunc();
      return result; // If successful, return the result
    } catch (error) {
      lastError = error;
      if (i < retries - 1) {
        // Wait before retrying
        await new Promise((resolve) => setTimeout(resolve, delay));
      }
    }
  }
  // After exhausting all retries, throw the last error encountered
  throw lastError;
};

export default fetchBotConfigs;