import axios from "axios";

export async function getAllConversation(botId) {
    try {
        const response = await axios.get(
            `${process.env.REACT_APP_NEST_BACK_URL}/mybots/${botId}/conversations/session`,
            {
              withCredentials: true, 
            }
          );

        const data = response.data;
        return data
      } catch (error) {
        console.error("Error:", error);
      }
}



