const theme = createTheme({
  palette: {
    primary: {
      main: "#1277fd", // Replace 'your_custom_color' with your desired color
    },
  },
});

function App() {
  const isMobile = useMediaQuery({
    query: '(max-width: 470px)'
  })
  const widgetStore = useWidget((s) => s);
  const tokenStore = useToken((s) => s);
  const widgetCustomization = useWidgetCustomization();
  const { darkMode, uiConfigs } = useWidgetCustomization();

  const [isBotConfigLoading , setIsBotConfigLoading] = useState(true);
  const [hasErrorLoadingBotConfig , setHasErrorLoadingBotConfig] = useState(false)

  // const [searchParams] = useSearchParams();

    // useEffect(() => {
    //   Sentry.configureScope(scope => {
    //     scope.setTag("component", "WidgetReact"); 
    //     scope.setContext("Widget", {
    //       name: "WidgetReact",
    //       version: "1.0.0",
    //       environment: "production"
    //     });
    //   });
    // }, []);


//for set  chatbotId and SessionId
  useEffect(() => {
    const queryString = window.location.search;

    // Use URLSearchParams to parse the query string
    const urlParams = new URLSearchParams(queryString);
  
    // Get specific parameters
      const tokenFromLocal = urlParams.get('botRef');
      
      if (!tokenFromLocal) {
        console.error("Token is not defined");
      }
      try {
        tokenStore.setToken(tokenFromLocal);
      } catch (error) {
        console.error("Error:", error);
      }
  
  }, []);


//for check acticty and config
  useEffect(() => {
    if (!tokenStore.token) {
      // setIsBotConfigLoading(false)
      // setHasErrorLoadingBotConfig(true)
      return;
    }
  
    (async function loadConfigAndCheckActivity() {
      try {
        let configData = await fetchBotConfigs(tokenStore.token);
  
        // Check bot status
        const isActive = configData.status === "active";
         if(!isActive){
          return
         }
          widgetStore.fullyActive();
        // Load UI config
        const root = document.documentElement;
        root.style.setProperty(
          "--mybot-css-variable-name-for-scroll",
          configData?.uiConfigs?.userMsgBgColor
        );
        widgetCustomization.updateBotConfigs(configData);

        setIsBotConfigLoading(false)
        setHasErrorLoadingBotConfig(false)
  
      } catch (error) {
        // Error handling for both bot status and UI config
        setIsBotConfigLoading(false)
        setHasErrorLoadingBotConfig(true)
        widgetCustomization.initiateDefaultTheme();
        console.log("ERROR FETCHING DATA:", error);
      }
    })();
  }, [tokenStore.token]);
  

 //for dark mode 
  useEffect(() => {
    const root = document.documentElement;
    if (darkMode) {
      root.style.setProperty('--bot-theme-primary-background-color', '#000000');
      root.style.setProperty('--bot-theme-secondary-background-color', '#2c2c2c');
      root.style.setProperty('--bot-theme-text-color', '#ffffff');
    } else {
      root.style.setProperty('--bot-theme-primary-background-color', '#ffffff');
      root.style.setProperty('--bot-theme-secondary-background-color', '#f1f5f8');
      root.style.setProperty('--bot-theme-text-color', '#000');
    }
  }, [darkMode]);


  //for position 
  // useEffect(() => {
  //   const root = document.documentElement;
  //   if (uiConfigs?.botWidgetPosition === "right") {
  //     root.style.setProperty('--bot-position-right', isMobile ?'0':'12px');
  //     root.style.setProperty('--bot-position-left', 'unset');
  //     root.style.setProperty('--bot-position-top', 'unset');
  //     root.style.setProperty('--bot-position-bottom', isMobile ?'0':'12px');

  //   } else {
  //     root.style.setProperty('--bot-position-right', 'unset');
  //     root.style.setProperty('--bot-position-left', isMobile ?'0':'12px');
  //     root.style.setProperty('--bot-position-top', 'unset');
  //     root.style.setProperty('--bot-position-bottom', isMobile ?'0':'12px');
  //   }
  // }, [uiConfigs , isMobile]);


  useEffect(() => {
    // Update the document title
    document.title = "دستیار همیارچت";

    // Update the favicon
    const favicon = document.querySelector("link[rel='icon']") || document.createElement('link');
    favicon.rel = 'icon';
    favicon.href = 'https://hamyar.chat/logo.svg'; // Replace with your favicon path
    document.head.appendChild(favicon);
  }, []);


  return (
    <ThemeProvider theme={theme}>
      <div
        id={"HAMYARBOT"}
      >

        {/* {tokenStore.token && widgetStore.isFullyActive && widgetCustomization.configIsLoaded ? (
          widgetStore.isActive ? (
            <ChatTemplate/>
          ) : (
            <Widget />
          )
        ) : null} */}
        <ChatTemplate/>
        {
  hasErrorLoadingBotConfig ? (
    <div className="absolute top-0 right-0 left-0 bottom-0 z-[20000000000] flex flex-col justify-center items-center bg-white">
      <div>
      <Lottie
          animationData={errorAnimation}
          style={{ transform: "scale(0.7)" }}
          loop={true}
          autoPlay={true}
          rendererSettings={{
            preserveAspectRatio: "xMidYMid slice",
          }}
        />
      
      <p style={{ direction: "rtl" }} className="text-center">
  متاسفانه خطایی رخ داده است.
  <br/>
   برای اطلاعات بیشتر می‌توانید به وب‌سایت ما مراجعه کنید: <a href="https://hamyar.chat" target="_blank">همیارچت</a>
</p>
      </div>
    </div>
  ) : isBotConfigLoading ? (
    <div className="absolute top-0 right-0 left-0 bottom-0 z-[20000000000] flex flex-col justify-center items-center bg-white">
      <div>
        <Lottie
          animationData={loaderAnimation}
          style={{ transform: "scale(0.7)" }}
          loop={true}
          autoPlay={true}
          rendererSettings={{
            preserveAspectRatio: "xMidYMid slice",
          }}
        />
        <p style={{ direction: "rtl" }} className="text-center">
          به همیارچت خوش امدید.
          <br />
          درحال دریافت اطلاعات...
        </p>
      </div>
    </div>
  ) : null
}
      </div>
    </ThemeProvider>
  );
}

export default App;
