const CahtBotLoader = () => {
    return (
        <div className="loader">
            <Lottie
                animationData={animationData}
                height={80}
                width={80}
                loop={true}
                style={{ height: "30px" }}
                autoPlay={true}
                rendererSettings={{
                    preserveAspectRatio: "xMidYMid slice",
                }}
            />
        </div>
    )
}

