import "./style.css";
//import useHubspot from "../integrations/hubspot";
import {
  useController,
  useChat,
  useWidget,
  useToken,
  useWidgetCustomization,
  useBotImage,
} from "../store/chat.state";
import { fetchEventSource } from "@microsoft/fetch-event-source";
import Typing from "../utils/Typing";
import { Like1, Dislike } from "iconsax-react";
import { Box, Button, Typography, Avatar, Paper } from "@mui/material";

import Markdown from "react-markdown";
import { useEffect, useRef, useState } from "react";
import CahtBotLoader from "./CahtBotLoader";

const Message = ({ message, index }) => {
  //NOTE: operator logic must segregate 
  const isBot = message.sender === "bot" || message.sender === "operator";
  const isOperator = message.sender === "operator";


  const messages = useChat((s) => s.messages);
  const chat = useChat();
  const tokenStore = useToken();
  // const { isHSOpen, setIsHSOpen } = useHubspot();
  const widgetStore = useWidget();
  const widgetCustomization = useWidgetCustomization();
  const controllerStore = useController();
  const botImage = useBotImage();

  const [isAnimating, setIsAnimating] = useState(true);
  const [feedBackState, setFeedBackState] = useState(null);

  const handleLike = () => {
    setFeedBackState("like");
  };

  const handleDislike = () => {
    setFeedBackState("disLike");
  };

  const handleSend = async (input) => {
    controllerStore.abort();

    chat.appendMessage({
      id: messages?.length,
      body: input,
      sender: "user",
      time: Date.now(),
    });

    try {
      chat.appendMessage({
        id: messages?.length,
        body: "",
        sender: "bot",
        hasFeedback: true,
        time: Date.now(),
      });

      const response = await fetchEventSource(
        `${process.env.REACT_APP_BACK_URL}/hamyarchat/stream`,
        {
          method: "POST",
          headers: {
            "Content-Type": "text/plain",
          },
          body: JSON.stringify({
            input: {
              init_question: input,
              collection_name: tokenStore.token,
            },
          }),
          signal: controllerStore.getSignal(),
          onmessage(msg) {
            if (msg.event === "data") {
              let messageData = JSON.parse(msg.data);
              chat.streamChunkToLastMessage(messageData);
            } else if (msg.event === "end") {
              chat.handleLastMassageFailure();
            }
          },
          onerror(err) {
            console.error("Error fetching data:", err);
            throw new Error("Error fetching data from SSE server");
          },
        }
      );
    } catch (error) {
      console.error("Error:", error);
      chat.streamChunkToLastMessage(
        "در حال حاضر با مشکلات فنی روبرو هستم. 😕 لطفا بعدا دوباره امتحان کنید یا یک چت زنده با تیم ما شروع کنید. 🛠️"
      );
    }
  };

  // const handleLiveChat = () => {
  //   setIsHSOpen(!isHSOpen);
  // };

  const messageRef = useRef();
  useEffect(() => {
    if (messageRef.current && isAnimating) {
      setIsAnimating(false);
    }
  }, [isAnimating]);

  return (
    <div>
      <div
        className={`flex ${isBot ? "mr-16" : "ml-16"} ${
          isBot ? "justify-start" : "justify-end"
        }`}
      >
        <div className="flex flex-col">
          <Box
            sx={{
              display: "flex",
              flexDirection: isBot ? "row" : "row-reverse",
              alignItems: "flex-end",
            }}
          >
            {isBot ? (
  isOperator ? (
    <Avatar
      style={{
        border: "1px solid blue",
        width: "32px",
        height: "32px",
      }}
      alt="Operator"
      src={"https://hamyar.chat/live.png"} // Replace with the operator's avatar URL
    />
  ) : (
    widgetCustomization.uiConfigs.botImage && !botImage.hasError ? (
      <img
        style={{
          border: `1px solid ${widgetCustomization.uiConfigs.userMsgBgColor}`,
          borderRadius: "50%",
          width: "24px",
          height: "24px",
          display: botImage.hasError ? "none" : "block",
        }}
        alt={widgetCustomization.uiConfigs.botName}
        src={widgetCustomization.uiConfigs.botImage}
        onError={() => botImage.handleError()} // Set the error handler
      />
    ) : (
      <Avatar
        style={{
          border: "",
          width: "32px",
          height: "32px",
        }}
        alt={widgetCustomization.uiConfigs.botName}
        src={"https://hamyar.chat/cdn/widget/v1.0.0/sourceWidget/public/botface.svg"}
      />
    )
  )
) : null}


            <Paper
              variant="outlined"
              className="default-primary-theme-force"
              sx={{
                py: 1,
                px: 2,
                ml: isBot ? 1 : 0,
                mr: isBot ? 0 : 1,
                border: "none",
                background: isBot
                  ? "unset"
                  : widgetCustomization.uiConfigs.userMsgBgColor +
                    " !important",
                color: isBot
                  ? //"#00000"
                    "unset"
                   : widgetCustomization.uiConfigs.userMsgColor + "!important",
                borderRadius: isBot
                  ? "1.25rem 1.25rem 1.25rem .25rem"
                  : "1.25rem 1.25rem .25rem 1.25rem",
                minHeight: "25px",
                minWidth: "70px",
                boxShadow:
                  "0 .25rem 6px rgba(50, 50, 93, .08), 0 1px 3px rgba(0, 0, 0, .05)",
              }}
              ref={messageRef}
              style={{
                transformOrigin: isBot ? "left center" : "right center",
                transform: isAnimating ? "scale(0)" : "scale(1)",
                opacity: isAnimating ? 0 : 1,
                transition: "transform .35s ease, opacity .35s ease ",
                willChange: "transform, opacity",
                maxWidth: "280px",
              }}
            >
              <div 
              style={{
                direction: "rtl"
              }}
              className="bot-custom-iran-sans transition-all duration-300 ease-in-out markdown-styles">
                {message?.body ? (
                  <Markdown className={""}>{message?.body}</Markdown>
                ) : isBot ? (
                  <CahtBotLoader />
                ) : (
                  ""
                )}
              </div>
            </Paper>
          </Box>
          <div
            className={`flex mb-2 items-center gap-3 ${
              isBot ? "ml-8" : "mr-4"
            } ${isBot ? "justify-start" : "justify-end"}`}
          >
            <div>
              <span className="text-xs text-gray-500 opacity-50" dir="ltr">
                {new Intl.DateTimeFormat("en-US", {
                  dateStyle: "short",
                  timeStyle: "short",
                }).format(new Date(message?.time))}
              </span>
            </div>
            {isBot && message?.body && message?.hasFeedback && (
              <>
                <div className="flex">
                  <button
                    onClick={handleLike}
                    type="button"
                    className="inline-flex flex-shrink-0 justify-center items-center size-5 rounded-full text-gray-500 hover:bg-blue-100"
                  >
                    <Like1
                      size="14"
                      color="green"
                      variant={feedBackState == "like" ? "Bold" : "Outline"}
                    />
                  </button>

                  <button
                    onClick={handleDislike}
                    type="button"
                    className="inline-flex flex-shrink-0 justify-center items-center size-5 rounded-full text-gray-500 hover:bg-blue-100"
                  >
                    <Dislike
                      size="14"
                      color="red"
                      variant={feedBackState == "disLike" ? "Bold" : "Outline"}
                    />
                  </button>
                </div>
              </>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Message;
